import { collection, doc, onSnapshot, query, where } from "firebase/firestore";
import { useEffect, useState } from "react";
import { NavLink, useNavigate, useParams } from "react-router-dom"
import { db } from "../firebase/config";

export default function Followers() {
	const { userId } = useParams();
	const [user, setUser] = useState(null);
	const [followers, setfollowers] = useState([]);
	const navigate = useNavigate();

	useEffect(()=>{
		const unsub = onSnapshot(doc(db, "users", userId), (doc) => {
			setUser({id:doc.id, ...doc.data()});
	});
		return unsub;
	},[userId]);

	useEffect(()=>{
		let unsub = undefined
		if(user){
			const q = query(collection(db,'users'),where("following", "array-contains", user.userId))
			unsub = onSnapshot(q, (querySnapshot) => {
				const data = [];
				querySnapshot.forEach((doc) => {
					data.push({id:doc.id,...doc.data()});
				});
				setfollowers(data)
			});
		}
		return unsub
	},[user])
	
  return (
      <div className="">
				<div className="flex font-medium text-slate-400 border-b border-gray-600">
					<NavLink to={'/p/'+userId+'/followers'} end className="flex-1 hover:bg-[#1C2732] cursor-pointer text-center py-4">
						{({ isActive }) => (
							<span 
								className={ isActive ? 'border-b-4 text-white font-bold border-blue-400 p-3 rounded-sm' : undefined
								}
							>
								Followers
							</span>
						)}

					</NavLink>
					<NavLink to={'/p/'+userId+'/following'} className="flex-1 hover:bg-[#1C2732] cursor-pointer text-center py-4">
						{({ isActive }) => (
							<span 
								className={ isActive ? 'border-b-4 text-white font-bold border-blue-400 p-3 rounded-sm' : undefined
								}
							>
								Following
							</span>
						)}

					</NavLink>
				</div>
				<ul role="list" className="mt-6 ">
					{
						followers.map(follower => (
						<li key={follower.id} onClick={()=>navigate(`/p/${follower.id}`)} className="flex p-4 hover:bg-[#1C2732] cursor-pointer">
							<img className="h-10 w-10 rounded-full" src={false||`https://avatars.dicebear.com/api/initials/${follower.fullName}.svg`} alt="" />
							<div className="ml-3 overflow-hidden whitespace-nowrap">
								<p className="text-sm font-semibold">{follower.fullName}<span className="text-sm font-medium text-slate-400 truncate"> @{follower.username}</span></p>
							</div>
						</li>
						))
					}

  	    </ul>
      </div>
  )
}
