
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { Link, NavLink, useNavigate, useParams } from "react-router-dom";
import { useContext, useEffect, useState } from "react";
import { addDoc, collection, doc, getDocs, onSnapshot, query, serverTimestamp, where } from "firebase/firestore";
import { db, removeFollowers, updateFollowers } from "../firebase/config";
import { UserContext } from "../UserContext";

export default function ProfileHeader() {
	const { userId } = useParams();
	const [user, setUser] = useState(null);
	const [isFollowing, setFollowing] = useState(false);
	const currentUser = useContext(UserContext);
	const [hover, setHover] = useState(false);
	const navigate = useNavigate();
	const onHover = () => {
		setHover(true);
	};

	const onLeave = () => {
		setHover(false);
	};
	useEffect(()=>{
		const unsub = onSnapshot(doc(db, "users", userId), (doc) => {
			setUser({id:doc.id, ...doc.data()});
	});
		return unsub;
	},[userId]);

	useEffect(()=>{
		if(user){
			setFollowing(user.followers.includes(currentUser.userId))
		}
	},[user,currentUser])
	let followBtn = (
		<button onClick={() => updateFollowers(user,currentUser)} type="button" className="text-center h-10 px-6 ml-2 text-base text-black rounded-full transition font-bold bg-white hover:bg-slate-200">
			Follow
		</button> 
	)
	async function handleMessage(){
		
		const q = query(collection(db, "rooms"), where("members", "array-contains", userId));
		const querySnapshot = await getDocs(q);
		if( !querySnapshot.empty ){

			navigate('/messages/'+querySnapshot.docs[0].id)
		}
		else{
			const room = await addDoc(collection(db, 'rooms'), {
				members: [userId,currentUser.id],
				lastMsg: {timestamp:serverTimestamp()} 
			});
			navigate('/messages/'+room.id)
		}
	}

	let followingBtn = (
		<button 
		onClick={() => removeFollowers(user,currentUser)} 
		onMouseEnter={onHover}
		onMouseLeave={onLeave}
		type="button" 
		className="text-center h-10 px-6 ml-2 text-base text-black rounded-full transition font-bold bg-white hover:bg-transparent hover:border-2 border-pink-700 hover:text-pink-700">
			{hover ? "Unfollow" : "Following"}
		</button> 
	)
  return (
      <div className="">
          <div className="h-48 bg-gray-500 relative -z-10">
						<img className="h-28 w-28 -z-0 border border-[#15202B] transition hover:scale-105 cursor-pointer border-4 ml-4 absolute -bottom-14 object-cover rounded-full" src={0||`https://avatars.dicebear.com/api/initials/${user&&user.fullName}.svg`} alt="Current profile photo" />
					</div>
          
          <div className="flex flex-row-reverse p-4">
						{ (userId!=currentUser.id)&&(isFollowing? followingBtn:followBtn) } 

						{(userId!=currentUser.id)&&<button onClick={handleMessage} className="transition h-10 w-10 flex items-center justify-center border border-gray-400 rounded-full hover:bg-gray-600 hover:scale-105">
								<FontAwesomeIcon className="text-xl" icon={faEnvelope} />
						</button>}
						{(userId==currentUser.id)&&<div className="h-10"></div>}
						
          </div>
					<div className="p-4 text-base text-left overflow-hidden">
							<p className="font-semibold">{user&&user.fullName}</p>
							<p className="font-medium text-slate-400">@{user&&user.username}</p>
							<div className="text-sm flex mt-4">
								<Link to={'/p/'+userId+'/following'} className="font-semibold">{ user&&user.following.length } <span className="font-medium text-slate-400">Following</span></Link>
								<Link to={'/p/'+userId+'/followers'} className="ml-4">{ user&&user.followers.length } <span className="font-medium text-slate-400">Followers</span></Link>
							</div>
					</div>
					<div className="flex font-medium text-slate-400 border-b border-gray-600">
						<NavLink to={'/p/'+userId} end className="flex-1 hover:bg-[#1C2732] cursor-pointer text-center py-4">
							{({ isActive }) => (
								<span 
									className={ isActive ? 'border-b-4 text-white font-bold border-blue-400 p-3 rounded-sm' : undefined
									}
								>
									Tweets
								</span>
              )}

						</NavLink>
						<NavLink to='likes' className="flex-1 hover:bg-[#1C2732] cursor-pointer text-center py-4">
							{({ isActive }) => (
								<span 
									className={ isActive ? 'border-b-4 text-white font-bold border-blue-400 p-3 rounded-sm' : undefined
									}
								>
									Likes
								</span>
              )}

						</NavLink>
					</div>
      </div>
  )
}
